import { observer } from 'mobx-react';
import React from 'react';

import { IActionContext } from '@msdyn365-commerce/core-internal';
import GetProductByCategory, { ProductsByCategoryInput } from '../actions/get-products-by-category';

/*@internal*/
@observer
export default class ProductSearchList extends React.Component<any, any> {
    constructor(props: any) {
        super(props);
        this.state = { p1: [], p2: [] };
    }

    public async componentWillMount() {
        const ctx = this.props.actionContext as IActionContext;
        const [p1, p2] = await Promise.all([
            GetProductByCategory(new ProductsByCategoryInput('', 'AA3'), ctx),
            GetProductByCategory(new ProductsByCategoryInput('C103', ''), ctx)
        ]);

        this.setState({
            p1: p1,
            p2: p2
        });
    }

    public render(): JSX.Element {
        const { p1 } = this.state;
        return <div>{JSON.stringify(p1)}</div>;
    }
}
